import Vue from 'vue';
import Router from 'vue-router';

Vue.use(Router);

export default new Router({
	routes: [
		{
			path: '/404',
			component: resolve => require(['../page/404.vue'], resolve),
			hidden: true
		},
		{
			path: '/login',
			component: resolve => require(['../page/Login.vue'], resolve),
			hidden: true
		},
		{
			path: '/',
			redirect: '/users'
		},
		{
			path: '/users',
			name: '用户管理',
			component: resolve => require(['../page/Home.vue'], resolve),
			redirect: '/users/students',
			level: 1,
			children: [
				{
					path: '/users/students', 
				  	component: resolve => require(['../page/user/Students.vue'], resolve), 
				  	name: '学生列表',
				  	level: 2,
				  	leaf: true
				},
				{
					path: '/users/teachers', 
				  	component: resolve => require(['../page/user/Teachers.vue'], resolve), 
				  	name: '教职工列表',
				  	level: 2,
				  	leaf: true
				},
				{
					path: '/users/add', 
				  	component: resolve => require(['../page/user/Add.vue'], resolve), 
				  	name: '添加学生',
				  	level: 2,
				  	leaf: true
				}
			]
		},
		{
			path: '/others',
			name: '其他管理',
			component: resolve => require(['../page/Home.vue'], resolve),
			redirect: '/others/content',
			level: 1,
			children: [
				{
					path: '/others/content',
				  	name: '内容管理',
				  	level: 2,
				  	component: resolve => require(['../page/Second.vue'], resolve),
				  	redirect: '/others/content/editor',
				  	children: [
				  		{
				  			path: '/others/content/editor',
						  	name: '富文本编辑器',
						  	level: 3,
						  	component: resolve => require(['../page/others/content/Editor.vue'], resolve),
						  	leaf: true
				  		},
				  		{
				  			path: '/others/content/upload',
						  	name: '图片上传',
						  	level: 3,
						  	component: resolve => require(['../page/others/content/Upload.vue'], resolve),
						  	leaf: true
				  		}
				  	]
				}
			]
		},
		{
	        path: '*',
	        hidden: true,
	        redirect: { path: '/404' }
	    }
	]
})


