function isJSON(data){
	return typeof(data) == 'object' && Object.prototype.toString.call(data).toLowerCase() == '[object object]' && !data.length
}
function toJSONStr(data){
	if(isJSON(data)){
		return JSON.stringify(data)
	}
	return data
}

let session = {
	get user(){
		return window.sessionStorage.getItem("user")
	},
	set user(newValue){
		window.sessionStorage.setItem("user",toJSONStr(newValue))
	}	
}

let auth = {
	loggedIn() {
		return session.user;
	}
}

/*
* Date格式化
* (new Date()).Format("yyyy-MM-dd hh:mm:ss.S") ==> 2006-07-02 08:09:04.423 \
* (new Date()).Format("yyyy-M-d h:m:s.S")      ==> 2006-7-2 8:9:4.18 
*/
Date.prototype.Format = function(fmt){
var dt = {
	'M+' : this.getMonth() + 1,
	'd+' : this.getDate(),
	'h+' : this.getHours(),
	'm+' : this.getMinutes(),
	's+' : this.getSeconds(),
	'S+' : this.getMilliseconds()
}
if(/(y+)/.test(fmt)){
	fmt = fmt.replace(RegExp.$1,(this.getFullYear()+'').substr(4-RegExp.$1.length));
}
for(var o in dt){
	if(new RegExp('('+ o +')').test(fmt)){
		fmt = fmt.replace(RegExp.$1,(RegExp.$1.length == 1) ? dt[o] : ('00' + dt[o]).substr(('' + dt[o]).length));
	}
}
return fmt;
};

export default {
	session: session,
	auth: auth,
	isPhone: function(phone){
	    return /^(13[0-9]|14[0-9]|15[0-9]|18[0-9])\d{8}$/.test(phone);
	},
	isEmpty: function(str){
		str = str.replace(/(^\s*)|(\s*$)/g, '')
		if(str.length == 0)
			return true
		return false
	}
}


