import axios from 'axios';
import MockAdapter from 'axios-mock-adapter';
import {userLogin, students, teachers} from './data/user';

let _users = {
	'1': teachers,
	'2': students
}


export default {
	bootstrap() {
		let mock = new MockAdapter(axios);

		// 登录
		mock.onPost('/login').reply(config => {
			let {username, password} = JSON.parse(config.data);

			return new Promise((resolve, reject) => {
				let user = null;

				setTimeout(() => {
					let isUsers = userLogin.some(u => {
						if (u.username === username && u.password === password) {
			              const usertoken = Math.random().toString(36).substr(2);
			              user = JSON.parse(JSON.stringify(u));
			              delete user.password;
			              user.usertoken = usertoken;
			              return true;
			            }
					});

					if(isUsers){
						resolve([200, { code: 200, msg: '登录成功', user }]);
					}else{
						resolve([200, { code: 500, msg: '账号或密码错误' }]);
					}
				},1000);
			});
		});


		/* 获取用户列表
		 * @type: 1--教职工  2--学生
		 * @search: 搜索关键字
		 * @searchType: 搜索的类型   0--所有  1--姓名  2--班级
		 * @pagenum:  页码
		 */
		mock.onGet('/user/list').reply(config => {
			let {type, search, searchType, pagenum} = config.params;
			let mockUsers = _users[type].filter(user => {
				let hasName = user.name.indexOf(search) != -1;
				let hasClass = user.classroom.indexOf(search) != -1;
				if(searchType == '0'){
					return hasName || hasClass;
				}else if(searchType == '1'){
					return hasName;
				}else if(searchType == '2'){
					return hasClass;
				}
				return true;
			});
			let total = mockUsers.length;	
			mockUsers = mockUsers.filter((u, index) => index < 20 * pagenum && index >= 20 * (pagenum -1));
			return new Promise((resolve, reject) => {
				setTimeout(() => {
					resolve([200, {
						total: total,
						users: mockUsers
					}]);
				},1000);
			});
		});

		/* 删除用户
		 * @type: 1--教职工  2--学生
		 * @id: '1,2,3...'
		 */
		mock.onGet('/user/del').reply(config => {
			let {type, ids} = config.params;
			const idArr = ids.split(',');
			_users[type] = _users[type].filter(u => idArr.indexOf((u.id).toString()) == -1);
			return new Promise((resolve, reject) => {
		        setTimeout(() => {
		          resolve([200, {
		            code: 200,
		            msg: '删除成功'
		          }]);
		        }, 1000);
		    });
		});


		/* 修改用户信息
		 * @type: 1--教职工  2--学生
		 */
		mock.onGet('/user/update').reply(config => {
			let {type,data} = config.params;
			_users[type].some(u => {
				if(u.id == data.id){
					for(var d in data){
						u[d] = data[d];
					}
					return true;
				}
			});
			return new Promise((resolve, reject) => {
		        setTimeout(() => {
		          resolve([200, {
		            code: 200,
		            msg: '修改成功'
		          }]);
		        }, 1000);
		    });
		});

		/* 添加用户信息
		 * @type: 1--教职工  2--学生
		 * @data
		 */
		mock.onGet('/user/add').reply(config => {
			let {type,data} = config.params;
			_users[type].push(data);
			return new Promise((resolve, reject) => {
		        setTimeout(() => {
		          resolve([200, {
		            code: 200,
		            msg: '修改成功'
		          }]);
		        }, 1000);
		    });
		});
	}
}


