import Mock from 'mockjs';

const userLogin = [
	{
		id: 1,
		username: 'tresor',
		password: '123456'
	}
];


//学生列表
const students = [];

for(let i = 0; i < 66; i++){
	students.push(Mock.mock({
		'id': i+1,
		'sid|10000000-50000000': 1,
		'name': Mock.Random.cname(),
		'sexy': Mock.Random.integer(0, 1),
		'age|19-25': 1,
		'classroom|1': ['信息管理', '网页设计', '软件设计', '网络工程'],
		'phone': /^(13[0-9]|14[0-9]|15[0-9]|18[0-9])\d{8}$/,
		'regDate|1': ['2014-09-01','2015-09-01','2016-09-01'] 
	}));
}

//教职工列表
const teachers = [];

for(let i = 0; i < 30; i++){
	teachers.push(Mock.mock({
		'id': i+1,
		'name': Mock.Random.cname(),
		'sexy': Mock.Random.integer(0, 1),
		'age|28-65': 1,
		'phone': /^(13[0-9]|14[0-9]|15[0-9]|18[0-9])\d{8}$/,
		'classroom|1': ['信息管理', '网页设计', '软件设计', '网络工程'],
		'degree|1': ['专科','本科','研究生','博士'] 
	}));
}

export { userLogin, students, teachers }


